            height: 32px;
            border-radius: 3px !important;
            border: 1px solid $gray;
            background-color: $white;
            font-style: normal; }

        textarea.form-control {
            height: 100px;
            resize: none; }

        .profile-form {

            textarea {
                border: 0px transparent;
                background-color: $white;
                padding: 0;
                margin: 0 0 0 -5px; }

            .profile-submit {
                display: none; }

            &.edit {

                textarea {
                    border: 1px solid $gray;
                    background-color: $white;
                    padding: 5px;
                    margin: 5px; }

                .profile-submit {
                    display: block; } } } }

    .or {
