.modal-theme-1 {

    .modal-content {
        max-width: 100%;
        padding: 15px 5px; }

    .modal-header {
        margin: -15px -5px 0 0; } }

.login-signup-page, .faq-page, .about-us-page, .how-it-works-page, .contact-page {
    margin-top: 20%;
    padding: 0px 5px;

    h1 {
        font-size: 52px; } }

.contact-page p {
    font-size: 18px;
    margin: 20px auto; }

.what-do-want-to-do {
    padding: 50px 0px; }

.choose-buyer-traveler {
    padding: 0px 0 50px;

    .buyer_or_traveler {

        .buyer, .traveler {
            width: 90%;
            border-radius: 20px;
            min-height: 200px; } } }

.offer-inbox {
    flex-wrap: wrap;

    .users {
        min-height: 90px;
        max-height: 320px;
        flex: 1 1 100%;
        position: relative;
        border: 0px transparent;
        margin: 0;

        .xs-offer-inbox-toggler {
            width: 100%;
            padding: 5px 0;
            background-color: $lightGray;
            position: relative;

            .btn {
                width: 33.33%;
                margin: 0;
                border-left: 1px solid $gray;
                border-right: 1px solid $gray;
                background-color: transparent;
                position: relative;
                align-items: center;

                &:first-child {
                    border-left: 0px transparent; }

                &:last-child {
                    border-right: 0px transparent; }

                .counter {
                    font-size: 10px;

                    &:before {
                        content: "(";
                        font-size: 10px; }

                    &:after {
                        content: ")";
                        font-size: 10px; } } } }

        ul {
            max-width: 100%;

            &.minimized {
                max-height: 90px;
                overflow: hidden;
                transition: all 0.3s;

                li {
                    display: none; }

                li.active {
                    display: block; } }

            &.maximized {
                max-height: calc(100% - 40px);
                overflow: scroll;
                transition: all 0.3s; }

            li {
                border: 0px transparent;

                a {
                    width: 100%;
                    padding: 10px 2px;
                    justify-content: center;

                    img {
                        min-width: 20px;
                        max-width: 40px;
                        width: auto;
                        min-height: 20px;
                        max-height: 40px;
                        height: auto;
                        margin: 0;
                        font-size: 10px;
                        margin-right: 10px; }

                    p, b, span {
                        font-size: 12px;
                        justify-content: flex-start; } } } } }


    .inbox {
        margin-bottom: 0px;

        &.minimized {
            transform: translateX(-105%);
            transition: all 0.5s; }

        &.maximized {
            transform: translateX(0);
            transition: all 0.5s; }

        .list {

            .msg-list.active {
                overflow-y: scroll; }

            .item {
                padding: 10px;

                .time {
                    padding: 5px 15px; }

                .bubble {
                    width: 100%;
                    padding: 10px 20px;
                    margin-bottom: 17px;

                    .arrow {
                        -webkit-background-size: cover;
                        background-size: cover; } } }

            .item.right .bubble .arrow {
                bottom: -19px;
                height: 31px;
                left: 0px; } }

        #msg {
            margin: 1% 2%;
            width: 96%;
            height: 40px; }

        .send {
            padding: 0 4%;
            margin: 0 0 2px;

            .send-on-enter {
                font-size: 8px;
                text-align: left;
                justify-content: center;
                align-items: center;
                display: flex; } }

        h3 {
            border-right: 1px solid $gray;
            display: none;

            img {
                width: 12px; } } }

    .offers {
        min-height: 300px;
        border: 0px transparent;

        &.minimized {
            transform: translate(105%, -100%);
            transition: all 0.5s; }

        &.maximized {
            transform: translate(0, -100%);
            transition: all 0.5s; }

        .toggle-inbox, .toggle-offers {
            width: 0px;
            padding: 0;
            border: 0px transparent;
            overflow: hidden; }

        h3 {
            border-left: 1px solid $gray;
            display: none;

            img {
                width: 12px; } }

        .list .offer-list .item {
            padding: 3px;

            div a.btn {
                width: 65px;
                padding: 2px 5px;
                font-size: 12px; }

            .costs, .actions {
                max-width: 70px; }

            p {
                font-size: 12px; }

            b {
                font-size: 14px; } } } }



.client-nav-area {
