/** Product Search **/

.product-search {

    .form-group {
        margin: 10px 5px;

        .form-control {
            border-radius: 3px;
            background-color: $white;
            border: 1px solid $gray;
            height: 32px; }

        input[name=airport], input[name=country_from], input[name=country_to], input[name=airport] {
            width: 200px; }

        input[name=date_from], input[name=date_to] {
            width: 120px; }

        input[name=product_category] {
            width: 250px; } }

    h3, a[role="tab"] {
        color: $black;
        font-weight: 600; }

    ul[role="tablist"] {

        a, a:hover {
            text-decoration: none; }

        li {
            i.fa-check-square {
                display: none; }

            i.fa-square {
                display: inline-block;
                color: $blue; } }

        li.active {
            i.fa-check-square {
                display: inline-block;
                color: $blue; }

            i.fa-square {
                display: none; } } } }

/** END : Product Search **/

/** Footer area **/

footer {

