/** END : Client > Navs **/

/** User Dashboard **/

.continue-as-buyer, .continue-as-traveller {
    padding: 3% 10% !important;
    width: 39%;
    margin: 5%;
    display: inline-block;

    i {
        font-size: 9vw;
        display: block; }

    a {
        height: 45px;
        padding-top: 11px;
        font-size: 1.8vw;
        margin-top: 15px; } }

.continue-as-buyer {
    background: $white none center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover; }

.continue-as-traveller {
    background: $white none center center no-repeat;
    -webkit-background-size: cover;
    background-size: cover; }


/** END: User Dashboard **/

.airport-search {
    min-width: 250px; }

/** User Panel **/

.user-panel {

    * {
        color: $black; }

    .form-control {
        border: 1px solid $gray;
        margin: 5px auto;
        border-radius: 3px;
        background-color: $white;
        color: $black;
        height: 32px; }

    .upload-image {
        display: inline-flex;
        height: 35px; }

    input.btn {
        border-radius: 4px;
        margin: 20px 0; }

    input.btn.btn-lg {
        padding: 12px 30px; }

    .table.table-responsive {

        thead {

            tr {
                th {
                    background-color: $gray;
                    text-align: center; }

                th:first-child {
                    border-radius: 3px 0 0 0; }

                th:last-child {
                    border-radius: 0 3px 0 0; } } }

        tbody {

            tr {

                td {
                    background-color: $white;
                    border-bottom: 1px solid $gray;
                    text-align: center; } } } } }




/** END : User Panel **/


/** Search area**/

.search-area {
    padding-left: 0px !important;

    .search-heading {
        background-color: rgba(0, 0, 0, 0.7);
        color: $white;
        border-bottom: 7px solid $blue;
        padding: 15px 10px;
        margin: 0; }

    ul {
        background-color: $white;
        color: $black;
        margin-bottom: 20px;
        padding: 0;
        max-height: 200px;
        overflow-x: hidden;
        overflow-y: scroll;
        -webkit-transition: max-height 0.3s linear;
        -moz-transition: max-height 0.3s linear;
        -ms-transition: max-height 0.3s linear;
        -o-transition: max-height 0.3s linear;
        transition: max-height 0.3s linear;

        li {
            list-style: none;
            padding: 7px 10px;

            .date-range {
                width: 70px;
                display: inline-block; }

            input {
                border: 0px transparent; }

            input[type=checkbox], input[type=radio] {
                transform: translateY(2px);
                margin-right: 5px; } }

        li.search-for-buyer {
            padding: 0; } } }

.search-result-area {

    .img-holder {
        background-color: $white;
        height: 250px;
        position: relative;
        overflow: hidden;

        p {
            position: absolute;
            bottom: 0;
            min-height: 50px;
            width: 100%;
            background-color: rgba(1,1,1,0.8);
            color: $white;
            margin: 0;
            padding: 15px 0; }

        img {
            width: 100%; } }

    .search-item {

        h4 {
            height: 50px;
            overflow: hidden; } }

    p {
        margin: 0; }

    h4 {
        margin: 10px 0 0;
        font-weight: 600; } }

/** END : Search area **/


/** Message area **/

.messages {

    .message-list {
        height: 400px;
        overflow-x: hidden;
        overflow-y: scroll;

        .list-group {

            .list-group-item {
                border-radius: 0;
                padding: 8px;

                h4 {
                    padding: 0;
                    margin: 0;
                    font-weight: 400;
                    font-size: 14px;
                    width: 78%; }

                p {
                    width: 78%;
                    margin-top: 5px;
                    line-height: 15px; }

                .date {
                    position: absolute;
                    top: 7px;
                    right: 3px;
                    width: 22%;
                    font-size: 10px;
                    text-align: right; }

                a, a:hover {
                    text-decoration: none; }

                .unread {

                    h4, p {
                        font-weight: 700; } } }

            .list-group-item.active {
                background-color: $blue;

                * {
                    color: $white; } }

            .list-group-item:first-child {
                border-left: 0;
                border-right: 0; } } }

    .message-details {
        height: 450px;
        overflow-x: hidden;
        overflow-y: scroll;

        .message {
            height: 400px;
            overflow-x: hidden;
            overflow-y: scroll;
            margin-left: 20px;
            padding: 20px;

            h4 {
                font-size: 12px;
                font-weight: 600;
                line-height: 10px; } }

        .btn {
            color: $white;
            border-radius: 0;
            margin: 15px 0; } } }

/** END : Message area **/

/** Traveller Search **/

.green-modal {

    .modal-dialog {
        padding-top: 5%;

        .modal-content {
            border: 0px transparent;
            border-radius: 3px;
            background-color: $gray;
            text-align: center;
            color: $white;
            padding: 5% 7%;

            p, button {
                margin: 6% auto; }

            .submit-request {
                background-color: $blue;
                color: $white;
                border-radius: 0; }

            input.offer-price {
                background-color: $blue; } } } }

/** END : Traveller Search **/

.breifing {
    background-color: $white;

    p {
        font-size: 20px;
        text-align: center; }

    iframe {
        width: 560px;
        height: 315px; } }

